import React from 'react';

const ChangePass=({load, Loading, onSubmitPassword, OnPasswordChange, onConfirmPasswordChange})=>{
return(
<article className="br3 tc bg-white ba black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">
<main className="pa4 black-80">
		<div className="measure center">
		<Loading color="bg-near-black" load={load} x='25%' y='25%'/>
		<fieldset id="sign_up" className="ba b--transparent ph0 mh0">
		<legend className="f1 fw6 ph0 mh0">Set Password</legend>
		<div className="mt3">
		
		</div>
		<div className="mv3">
		<label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
		<input 
		className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="password"
		name="password"  
		id="password"
		onChange={OnPasswordChange}
		/>
		</div>
		<div className="mv3">
		<label className="db fw6 lh-copy f6" htmlFor="password">Confirm Password</label>
		<input 
		className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="password"
		name="password"  
		id="password"
		onChange={onConfirmPasswordChange}
		/>
		</div>
		</fieldset>
		<div className="">
		<input className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" onClick={onSubmitPassword} type="submit" value="Submit password"/>
		</div>
		</div>
</main>
</article>
)
}

export default ChangePass;