import React ,{PureComponent} from 'react';
import Boxlist from './boxlist/boxlist';
import Year from './year';
import Menu from './menu';
import Profile from './profile';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import {Route,Switch} from 'react-router-dom';
import QuestionBox from './questioninput/QuestionBox/QuestionBox'
import Loading from '../loading.svg';

var answer=[];
var showl=[];
let count=0;
let count1=0;
let countq=0;
var filterques=[{
				id:'',
				question:'',
				name:'',
				email:'',
				year:'',
				catagory:'',
				branch:'',
				joined:'',
				gender:'',
				show:''
			}];
var showa=[];
const intialdata={
			que:[{
				id:'',
				question:'',
				name:'',
				email:'',
				year:'',
				catagory:'',
				branch:'',
				joined:'',
				gender:'',
				show:'',
				like:''
			}],
			ans:[{
				qid:'',
				email:'',
				name:'',
				answer:'',
				year:'',
				bracnh:'',
				joined:'',
				gender:'',
				like:''
			}],
			answers:true,
			show:{
				display:'none'
			},
			showq:{
				display:'none'
			},
			selectedU:'',
			is:false,
			likeq:[],
			likea:[],
			key:Math.random(),
			userLikes:[{
				qid:''
			}],
			showlike:false
					}
var likes=[]

let answered='';
class Feed extends PureComponent{
	constructor(props){
		super(props);
		this.state=intialdata
	}


	ifAvailable=()=>{
this.setState({answers:false});
	}

	toShow=(e)=>{
		
		if(count%2===0){
		this.setState({show:{display:e}})}
		else{this.setState({show:{display:'none'}})}
		count++
	}

	toShowq=(e)=>{
		
		if(count1%2===0){
		this.setState({showq:{display:e}})}
		else{this.setState({showq:{display:'none'}})}
		count1++
		}

		isCalled=()=>{
		}

	onYearSelect=(value)=>{
			this.props.IsCalled();
		this.props.time(true);
		fetch('https://quiet-escarpment-25332.herokuapp.com/year',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			year:value
		})
		}).then(response=>response.json())
			.then(que=>
				{
					this.props.time();
					
					this.setState({que:que})
				})
	}

	
	onAnswerLike=(aid,email,i)=>{
		this.props.time(true)
		fetch('https://quiet-escarpment-25332.herokuapp.com/alike',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			aid:aid,
			email:email
		})
		}).then(response=>response.json())
		.then(data=>{
			if(data==='liked'){
			

				NotificationManager.success('Successfully', 'Liked');
          
				var la=this.state.likea
				la[i]=la[i]+1
				this.setState({likea:la})
				this.showlcall(i,true)
				this.props.time()
			

			}
			else{
				
				NotificationManager.success('Successfully', 'Disliked');
				var la=this.state.likea
				la[i]=la[i]-1
				this.setState({likea:la})
				this.showlcall(i,false)
				this.props.time()
		
			}
			
		})
	}
	
selectUser=(email,name)=>{
	var users=this.props.users;
if(name==='Anonymous'){
	return(null)
}
else{
	if (users!==undefined){ 
		for (var i =0; i <= users.length-1; i++) {
			if(users[i].email===email){
				this.props.getUser(users[i])
	
	
	
			}
	this.setState({is:true})
	}}
}
}
onQuestionSelect=(value)=>{
	this.props.IsCalled();
		this.props.sets();
		this.props.time(false);
		this.setState({que:value})
	}


onCatagorySelect=(value)=>{
	
		this.props.time(true);
		fetch('https://quiet-escarpment-25332.herokuapp.com/catagory',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			catagory:value
		})
		}).then(response=>response.json())
		.then(que=>
				{
					this.props.time();
					this.setState({que:que})
				})
	}

	onAnswerChange=(event)=>{
		answered=event.target.value;
	}

	deleteAnswer=(id,answer)=>{
		this.props.time(true);
		fetch('https://quiet-escarpment-25332.herokuapp.com/da',{
				method:'post',
				headers:{'Content-Type':'application/json'},
				body:JSON.stringify({
				answer:answer,	
				qid:id
					
				})
			})
		
		.then(data=>{
			NotificationManager.success('Successfully', 'Answer deleted');
			fetch('https://quiet-escarpment-25332.herokuapp.com/answer',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(ans=>
				{
					this.setState({ans:ans});
					this.props.time();
				})
		})
}
	deleteQuestion=(id)=>{
		this.props.time(true);
		fetch('https://quiet-escarpment-25332.herokuapp.com/dq',{
				method:'post',
				headers:{'Content-Type':'application/json'},
				body:JSON.stringify({
					id:id
				})
			}).then(data=>{
			NotificationManager.success('Successfully', 'Question deleted!!');
			
				fetch('https://quiet-escarpment-25332.herokuapp.com/question',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(que=>{	
			this.setState({que:que});
			this.props.time();
		})
		})
.catch(d=>{this.props.OnResponse('Error deleting!!')
this.props.time();
})}

	onSubmitAnswer=(id)=>{
		this.props.time(true);
		fetch('https://quiet-escarpment-25332.herokuapp.com/pa',{
				method:'put',
				headers:{'Content-Type':'application/json'},
				body:JSON.stringify({
					qid:id,
					email:this.props.user.email,
					name:this.props.uname,
					answer:answered,
				})
			}).then(data=>{
				this.props.time();
				NotificationManager.success('Successfully', 'Answer submitted');
			
			fetch('https://quiet-escarpment-25332.herokuapp.com/answer',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(ans=>
				{
					this.setState({ans:ans});
				})
		}).catch(d=>{
			
  	NotificationManager.error('Error ', 'Failed submitting Answer', 5000, () => {
            alert('callback');
          });
})
			}

showlk=()=>{
	for (var i = this.state.que.length - 1; i >= 0; i--) {
		showl.push(false)
		for (var j = this.state.userLikes.length - 1; j >= 0; j--) {
			if(this.state.que!==null&&this.state.que[i].id===this.state.userLikes[j].qid){
				showl[i]=true	
		}
		}
		
	}
}
showlcall=(i,t)=>{
	showl[i]=t
	this.setState({key:Math.random()})
}

showUpdate=()=>{
	for (var i = 0; i <= this.state.que.length-1; i++) {
		showa.push(this.state.que[i].show)
		}
		
}
ShowCall=(i,t)=>{
	showa[i]=t
	this.setState({key:Math.random()})
	}

	

componentWillUpdate(nextProps,prevState){
	if(prevState.que!==this.state.que){
		this.setState({ql:true})
	}

}
componentDidUpdate(nextProps){
	if(this.props.iu!==nextProps.iu)
{ this.selectUser(this.props.iu.email,this.props.iu.name)}
	
}
getAnswer=(ans)=>{
if(ans){
		answer=[]
		for (var i = ans.length - 1; i >= 0; i--) {
			if(answer[`${ans[i].qid}`]===undefined){
					var array=[]
					array.push(ans[i])
					answer[`${ans[i].qid}`]=array}
					else{
						var array=[]
						array=answer[`${ans[i].qid}`]
						array.push(ans[i])
						answer[`${ans[i].qid}`]=array;
					}
		}
		this.setState({ans:answer})}
	else{
		answer=[]
		for (var i = this.state.ans.length - 1; i >= 0; i--) {
			if(answer[`${this.state.ans[i].qid}`]===undefined){
					var array=[]
					array.push(this.state.ans[i])
					answer[`${this.state.ans[i].qid}`]=array}
					else{
						var array=[]
						array=answer[`${this.state.ans[i].qid}`]
						array.push(this.state.ans[i])
						answer[`${this.state.ans[i].qid}`]=array;
					}
		}
		this.setState({ans:answer})}
}

createQLike=()=>{
if(this.state.que!==undefined){
	this.state.que.map((quest)=>{
		likes[`${quest.id}`]=quest.like
		
	})
}

		this.setState({likeq:likes})
}

editLike=(id,value)=>{
	if (value) {
		likes[id]=likes[id]+1
		this.setState({likeq:likes})
	}
	else {likes[id]=likes[id]-1
	
		this.setState({likeq:likes})
	}

		this.setState({key:Math.random()})
	console.log(this.state.likeq[id])
}


getData=()=>{
fetch('https://quiet-escarpment-25332.herokuapp.com/question',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(que=>{	
			this.setState({que:que});	
				this.createQLike()
		})
fetch('https://quiet-escarpment-25332.herokuapp.com/answer',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(ans=>
				{
					
					this.setState({ans:ans});
						
				this.getLikes()
				this.getAnswer()
				if (this.props.match.path==='/home'||this.props.match.path==='/HOME') {
						this.props.SetName()
				}
			
					
				})
}

getLikes=()=>{
	if(this.props.user!==null&&this.props.user.id!==''){			
				
			}
	fetch('https://quiet-escarpment-25332.herokuapp.com/ul',{
							method:'post',
							headers:{'Content-Type':'application/json'},
							body:JSON.stringify({
								email:this.props.user.email
							})
						}).then(response=>response.json())
						.then(data=>{
							
						
						var lik=[]
						for (var i = data.length - 1; i >= 0; i--) {
							lik.push(data[i].qid)
						}
						this.setState({userLikes:lik})
						})
					}


	componentDidMount(){
				this.getData()	
		}

SelectQuestion=(id)=>{
	this.props.onsign(`${this.props.match.url}/QuestionBox/${id}`)
}

	render(){
			const {r, user, route, time}=this.props;
		const {answers, likea, que, ans, selectedU, is, show,key, showq, likeq, ql,userLikes,showlike}=this.state;

		

var far=[]
	if(this.props.ques!==undefined && this.props.ques!==null && this.props.t===undefined||false){
		
		filterques=this.state.que.map((que,i)=>{
			far=this.state.que[i].question.toLowerCase().match(this.props.ques.toLowerCase())
			if(far!==null){
						return(
						this.state.que[i])	
						}
						})
		filterques = filterques.filter(function( element ) {
   return element !== undefined;
});
		}
		
		if(this.props.t){
	this.onQuestionSelect(filterques)
	
}
if(this.props.isCalled){
		if(this.props.isCorY==='c'){
			this.props.IsCalled();
			this.onCatagorySelect(this.props.calld)

		}else{
		if(this.props.isCorY==='y'){
			this.props.IsCalled();
			this.onYearSelect(this.props.calld)
		}
	}
	}

	
return(
	<div className='db'>

	<Route path={`/home/QuestionBox/:id`}
						render={(props)=>(<QuestionBox {...props}
							SelectQuestion={this.SelectQuestion}
alike={likea}
onAnswerLike={this.onAnswerLike}
qlike={this.onQuestionLike}
user={this.props.user}
k={key}
selectUser={this.selectUser}
onAnswerChange={this.onAnswerChange}
onSubmitAnswer={this.onSubmitAnswer}
deleteAnswer={this.deleteAnswer}
deleteQuestion={this.deleteQuestion}
toShowq={this.toShowq}
showq={showq}/>)} />
	<Route path={`/main/QuestionBox/:id`}
						render={(props)=>(<QuestionBox {...props}
							alike={likea}
onAnswerLike={this.onAnswerLike}
qlike={this.onQuestionLike}
user={this.props.user}
k={key}
selectUser={this.selectUser}
onAnswerChange={this.onAnswerChange}
onSubmitAnswer={this.onSubmitAnswer}
deleteAnswer={this.deleteAnswer}
deleteQuestion={this.deleteQuestion}
toShowq={this.toShowq}
showq={showq}/>)}/>
{que!==null?

<div>

{que.map((user ,i) => {
	if(que[0].id===undefined||que[0].id===''||answer===[]){

		return(
			<div className=' w-100 w-50-m w-40-l h4 tc pa4 mt3 mt4-m mt0 mr-3 center-l h-50 center-m  overflow-hidden' >
			<img src={Loading} height='50px' width='50px'/>
			</div>
			)

	}else{
	if(que[i].catagory===null){
		que[i].catagory='Not Specified'
	}

	if(que[i].name==='Anonymous'){
		que[i].branch='';
		que[i].year='';
		que[i].pic=null;
	}
	/*if(que[i].id===260||que[i].id===256||que[i].id===121||que[i].id===169||que[i].id===128||que[i].id===132){null}
		else{*/		
	return(<div key={key+Math.random()}>	

<Boxlist 
getAnswer={this.getAnswer}
uname={this.props.uname}
time={this.props.time}
editLike={this.editLike}
userLikes={userLikes}
match={this.props.match}
SelectQuestion={this.SelectQuestion}
alike={likea}
onAnswerLike={this.onAnswerLike}
qued={que[i]}
showlike={showl[i]}
ql={likeq[que[i].id]}
qlike={this.onQuestionLike}
toShow={this.toShow}
ShowCall={this.ShowCall}
show={show}
qfile={que[i].file}
user={this.props.user}
route={route}
answers={answers}
question={que[i].question}
catagory={que[i].catagory}
scatagory={que[i].subcatagory}
answer={answer[`${que[i].id}`]}
index={i}
k={key}
selectUser={this.selectUser}
qshow={showa[i]}
id={que[i].id}
qj={que[i].joined}
qname={que[i].name}
qgender={que[i].gender}
qyear={que[i].year}
qemail={que[i].email}
qbranch={que[i].branch}
onAnswerChange={this.onAnswerChange}
onSubmitAnswer={this.onSubmitAnswer}
deleteAnswer={this.deleteAnswer}
deleteQuestion={this.deleteQuestion}
toShowq={this.toShowq}
showq={showq}
/>
{this.setState({showlike:false})}
	</div>
	)
}	})}
</div>:<div className='ba black-80  w-100 w-50-m w-40-l  mt3 mt4-m mt0 mr-3 center-l h-50 center-m  shadow-5 overflow-hidden' >
			No question found of such type
			</div>}
</div>
	)
}
	}
export default Feed;