import React , {Component} from 'react'
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import {Link} from 'react-router-dom';
import Slide from '@material-ui/core/Slide';
import TextField from '@material-ui/core/TextField';
const styles = theme => ({
    button: {
      margin: theme.spacing.unit*5,
      backgroundColor:'rgb(21, 10, 43);',
      width:theme.spacing.unit*20
    },
    input: {
      display: 'none',
    },
  });
  
 class Page2 extends Component{
    render(){
        return(
            <div className='mt6 mt7-l ma3-l '>
            <Slide direction="right" in={true} mountOnEnter unmountOnExit>
            <Card>
            <div className='ma4'><div>Please provide your name!</div>
            <TextField
          id="standard-name"
          label="Name"
          margin="normal"
        /></div>
         <br/> <div className='center mr-auto ml-auto'> <Link to='/survey/page3'><Button variant="contained" color="primary" className={this.props.classes.button}>
       <span className='white'> Continue</span>
      </Button></Link></div>
      </Card>
      </Slide>
            </div>
        )
    }
}
export default withStyles(styles)(Page2);