import React from 'react';
import Dot from './delete.jpg';
import m from '../../male.png';
import f from '../../female.png';
import TimeAgo from 'react-timeago';
import Switch from "react-switch";
import dislike from './notlike.png';
import like from './liked.png';
import Model from '../../../Model/Model';
import ReadMoreAndLess from 'react-read-more-less';
import './boxlist.css'
import Loading from '../../../loading/loading';
import {
  FacebookShareButton,
    FacebookIcon,
    WhatsappShareButton,
    WhatsappIcon,
     LinkedinShareButton,
     LinkedinIcon,
	  TwitterShareButton,
	  TwitterIcon,
} from 'react-share';

	var count=0;
	var alike=0;
	var question='';
	var answer='';
	class QuestionBox extends React.Component {
	constructor(props){
		super(props);
		this.state={
	question:'',
	answw:'',
	load:false,
	showa:false,
}
	}
	
	loadCall=()=>{
		this.setState({load:!this.state.load})
	}

	answerDelete=()=>{
		this.setState({showa:!this.state.showa})
	}

	getQuestion=()=>{
		fetch('https://quiet-escarpment-25332.herokuapp.com/questionid',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			qid:this.props.match.params.id
		})
		}).then(response=>response.json())
			.then(data=>{
				this.setState({question:data});
				question=data;
				this.getAnswer();
			})
	}

	getAnswer=()=>{
		
		fetch('https://quiet-escarpment-25332.herokuapp.com/answerid',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			qid:this.props.match.params.id
		})
		}).then(response=>response.json())
			.then(data=>{
				this.setState({answw:data});
				answer=data
			})
	}

	componentDidMount(){
		this.getQuestion();
		
		this.loadCall();
		
	}

	render(){
	const {match,onAnswerLike,showlike,selectUser,qshow, ShowCall, k, onAnswerChange, onSubmitAnswer, route, r, deleteAnswer, handleChange, checked, user, show, toShow, deleteQuestion, showq, toShowq, catagory}=this.props;
return(
	<Model show={1===1} className={"Modal2"}>
{ this.state.answw==='' ?
	<Loading color="rgb(21, 10, 43)" className='pt0 mt0 mb0' x='25%' y='30%' load={this.state.load}/>
	:(this.state.question[0]===undefined?null
		:<div className='mainDiv  w-100 w-100-m w-100-l bg-white mt2 mt4-m mt0 mr-3 center-l h-100 center-m  shadow-5 overflow-hidden' key={k}>
		

		<p className=' ml2 mt1 grey mb0 f6 fl clr pointer w-100' onClick={()=>{selectUser(this.state.question[0].email,this.state.question[0].name)}}>
	{false?<img src={`${this.state.question[0].pic}`} height='40em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${this.state.question[0].id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}
		{this.state.question[0].name}, {this.state.question[0].branch}, {this.state.question[0].year}, {this.state.question[0].catagory}, {this.state.question[0].subcatagory} <TimeAgo date={this.state.question[0].joined}  className='f7 black-40 fr pr3 mt1'/> </p>
		<h1 className=' w-100 f4 f4-ns  f3-m f4-l ma2 pr3 pb3 fl bb b--light-silver'>Q. {this.state.question[0].question} ? 
			<br/>
		<div key={k+9}>{this.state.question[0].file!==null?<img src={`${this.state.question[0].file}`} className='center h4 h5-l h5-m pa2' height='260'/>:null}</div>
	     <p className='ma0 pa0 pt2 f7 black-40 pointer' key={k+1}>{this.state.question[0].like} likes</p>
	     <div className='pt1'>
	     {match.path==='/home/QuestionBox/:id'?<div key={k+3}>{showlike===true?<img key={k+32} src={like}
	           height='40em '
	           className='fl grow pointer'
	          onClick={()=>{this.props.qlike(this.state.question[0].id,user.email,this.props.match.params.id)}}/>:<img key={k+31} src={dislike}
	           height='40em '
	           className='fl grow pointer'
	          onClick={()=>{this.props.qlike(this.state.question[0].id,user.email,this.props.match.params.id)}}
	          /> }</div>:null}
	     	   <div className='fr w-40 flex'> 
         <FacebookShareButton url={`https://easystudies.co/#/main/QuestionBox/${this.state.question[0].id}`}>
          <FacebookIcon size={32} round={true}/>
          </FacebookShareButton>
          <WhatsappShareButton url={`https://easystudies.co/#/main/QuestionBox/${this.state.question[0].id}`}>
          <WhatsappIcon size={32} round={true}/>
          </WhatsappShareButton>
          <LinkedinShareButton url={`https://easystudies.co/#/main/QuestionBox/${this.state.question[0].id}`}>
          <LinkedinIcon size={32} round={true}/>
          </LinkedinShareButton>
          <TwitterShareButton url={`https://easystudies.co/#/main/QuestionBox/${this.state.question[0].id}`}>
            <TwitterIcon size={32} round={true}/>
          </TwitterShareButton>
          </div>
    </div>

				{match.path==='/home/QuestionBox/:id'&&this.state.question[0].email===user.email?
										<div><img key={Math.random()} alt='' className='relative grow fr pr2 pl4 pointer' onClick={()=>toShowq('inline-block')} src={Dot}  height='20' width='20'/>					
							<div style={showq} key={Math.random()} className='ta ba absolute bg-light-gray f7 fr ml7-l mt7-m ml6 mt v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
							<h className='pb1'>Do you want to delete the question?</h>				<br/>	<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteQuestion(this.state.question[0].id)}>	Delete</button><br/>
							<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>toShowq('none')}>	Cancel</button></div>
							</div>	:<p/>}</h1>
							
	{this.state.answw.map((answer,i)=>{
		count++;
			if(answer.name==='Anonymous'){
			answer.branch='';
			answer.year='';
			answer.pic=null;
		}
		alike=answer.like	
			
				
			return(<div key={Math.random()} className='answer ma2 flex flex-column w-100 mt1 mb0  bl b--black-40'  >
				
				<p className='ma0 pa0'><span key={Math.random()} className='pointer grey ml0 mb0 pb0 i fl f7 f6-m f6-l' onClick={()=>{selectUser(answer.email,answer.name)}}>{answer.pic!==null?<img src={`${answer.pic}`} height='40em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${answer.id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}{answer.name.toUpperCase()}, {answer.branch}, {answer.year} 
				</span>
	<TimeAgo date={answer.joined}  className='f7 black-40 mb0 mt0 pb0 pt1 pr3 fr'/>
	 {match.path==='/home/QuestionBox/:id'?<div><span className='fr pa1 grow f7 f6-m f6-l grey pr2 pointer' onClick={()=>{onAnswerLike(answer.id,user.email,i)}}> Reply</span>
	 <span className='fr pa1 grow grey f7 f6-m f6-l pr2 pointer' onClick={()=>{onAnswerLike(answer.id,user.email,i)}}><span className='upvote'></span> {answer.like} Upvotes</span>
	 			
	 			</div>:null}		</p>
				 			 
				<span key={Math.random()} className='fl black ml2 f6 f5-m f5-l pr3 ma0 pa0 '>
				<p className='w-100 pt0 mt1  black-90'>
				{answer.answer} </p></span>
	         
				
				{match.path==='/home/QuestionBox/:id'&&answer.email===user.email?
										<div className='mt0 mb0 pt0 pb0'><img key={Math.random()} alt='' className='relative grow fl pl4 pointer mt0 mb0 pt0 pb0' onClick={()=>this.answerDelete()} src={Dot}  height='20' width='20'/>
								{this.state.showa?<div  key={Math.random()} className='ta ba absolute f7 fl bg-light-gray  v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
																<h className='pb1'>Do you want to delete the answer?</h><br/>	
																		<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteAnswer(answer.qid,answer.answer)}>	Delete</button><br/>
															<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>this.answerDelete()}>	Cancel</button></div>:null}
							</div>	:null}
							<p className='mt0 mb0 ml5  pb0 pt0'></p>
		
				</div>)
			
		})}
					{count===0?
												<p className='tc l mt0 mb0 pt0 pb0'>No replies yet!!</p>
												:<p></p>
												}			
	
	
	
	<br className='mt0 mb0 pt0 pb0'/>
			{match.path==='/home/QuestionBox/:id'&&user.verified===true?
			<div className='dib w-100'>
			
				<textarea
				placeholder='Answer'
				 className='fl dib border-box hover-black  measure ba b--black-20 pa2  br2 mb2 h2 w-70 w-40-ns w-50-m w-60-l pa2  ba bg-transparent hover-bg-black hover-white  ml3' 
				onChange={onAnswerChange}/>
				<button className='fl dib bg-near-black br1 v-top w-20 h2 pr2 white f6 grow shadow-5 bn' onClick={()=> onSubmitAnswer(this.state.question[0].id)}>Reply</button>
				
			</div>	
		:null
	}</div>)}</Model>
)
}
}

export default QuestionBox;
