import React from 'react';
import f from '../../female.png';
import m from '../../male.png';
import './userprofile.css';
import cross from './back.svg';
import Model from '../../../Model/Model';
import edit from '../../edit.ico'
import HeadCard from './Card/headCard'
import BasicCard from './Card/basicCard'
import QuestionCard from './Card/questionCard'
import Loading from '../../../loading.svg';
import FormData from 'form-data';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import EditProfile from './editProfile'
class userProfile extends React.Component {	
constructor(props){
		super(props);
		this.state={
			isFollow:false,
			iu:'',
			userFollowing:'',
			key:Math.random(),
			picUpload:false,
			file:'',
   		   formdata: new FormData(),
   		   load:false,
	}
	this.getData()
	
}
onFollowing=()=>{
	
		fetch('https://quiet-escarpment-25332.herokuapp.com/flwg',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
          email:this.props.user.email
             })
  }).then(response=>response.json())
        .then(d=>{this.setState({userFollowing:d})
   			d.map((follow,i)=>{
  	 	if(d[i].following===this.state.iu.email){
  	 	this.setState({isFollow:true})
  	 	this.setState({	key:Math.random()})
  
  	 }
  	 }) })

	
}

getData=()=>{
	fetch('https://quiet-escarpment-25332.herokuapp.com/userid',{
							method:'post',
							headers:{'Content-Type':'application/json'},
							body:JSON.stringify({
								id:this.props.match.params.id				
							})
						}).then(response=>response.json())
						.then(data=>{
							this.setState({iu:data});
							this.onFollowing()
						})
}

updateFollow=(t)=>{
if(t){
	let data=this.state.iu
        data.followers=data.followers+1
   		
    this.setState({iu:data})
						    
 	 	this.setState({isFollow:true})
}
else{
	let data=this.state.iu
        data.followers=data.followers-1
   		this.setState({iu:data})
		this.setState({isFollow:false})
}
}

followClick=()=>{

  	fetch('https://quiet-escarpment-25332.herokuapp.com/flw',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
        email2:this.state.iu.email,
        email1:this.props.user.email
             })
  }).then(response=>response.json())
  	.then(data=>{
  		if(data==='followed'){
  		fetch('https://quiet-escarpment-25332.herokuapp.com/flwer',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
        email2:this.state.iu.email,
        email1:this.props.user.email,
        inc:true
             })
    })
        fetch('https://quiet-escarpment-25332.herokuapp.com/flwng',{
        method:'post',
        headers:{'Content-Type':'application/json'},
        body:JSON.stringify({
        email2:this.state.iu.email,
        email1:this.props.user.email,
        inc:true
             })
    })
        
  	 this.updateFollow(true)
        NotificationManager.success('successfully', 'Followed');
       
  		}else{if(data==='unfollowed'){
  		  			fetch('https://quiet-escarpment-25332.herokuapp.com/flwer',{
  		        method:'post',
  		        headers:{'Content-Type':'application/json'},
  		        body:JSON.stringify({
  		        email2:this.state.iu.email,
  		        email1:this.props.user.email,
  		        inc:false
  		             })
  		        })
  		        fetch('https://quiet-escarpment-25332.herokuapp.com/flwng',{
  		        method:'post',
  		        headers:{'Content-Type':'application/json'},
  		        body:JSON.stringify({
  		        email2:this.state.iu.email,
  		        email1:this.props.user.email,
  		        inc:false
  		             })
  		        })
  		        
  		 this.updateFollow(false)
  	 	
  		        NotificationManager.success('successfully', 'Unfollowed');
  		      
  		    }
  		}
  	})
  }


picChose=(event)=>{
	this.setState({file:event.target.files[0]})
}

	uClick=()=>{
				this.setState({picUpload:!this.state.picUpload})
				}

	uploadClick=()=>{
		this.setState({load:true})
		var formdataU= new FormData();
		if(this.state.file!==''){
		formdataU.append('image',this.state.file);
		formdataU.append('uid',this.state.iu.id);
			fetch('https://quiet-escarpment-25332.herokuapp.com/upload',{
				method:'post',
				body:formdataU
				}).then(d=>{this.setState({load:false})
									NotificationManager.success('Sucessful', 'Profile pic Uploaded')
						this.setState({picUpload:!this.state.picUpload})
				
		}).catch(d=>{NotificationManager.error('Error ', 'Failed Uploading profile pic', 5000, () => {
		            alert('callback');
		          });
		this.setState({picUpload:!this.state.picUpload})
		this.setState({load:false})
				;})
				}
	}
	 

render(){
const	{email, k,  checkFollow,isFollowing,followClick,userFollowing,modalClosed}=this.props;
const {iu, key, picUpload}=this.state;

return(
<div className='stc' >{iu===undefined||iu===''?<div className=' w-100 w-50-m w-40-l h4 tc pa4 mt3 mt4-m mt0 mr-3 center-l h-50 center-m  overflow-hidden' >
			<img src={Loading} height='50px' width='50px'/>
			</div>:
<div >
{!picUpload?
<div  className='bg-white ba-black-80 center userp h7 overflow-y-scroll'  >
<div className='fl mt2-l bg-white ba b--black-10 br-100 h2 shadow-5 w2 pa1 grow pointer ma2 fixed' onClick={()=>{this.props.history.goBack()}}><img src={cross} className='center bg-white ml0 grow z-max fr' height='20px'  width='20px'/></div>
<br/>
<HeadCard iu={iu} edit={edit} email={email} uClick={this.uClick} followClick={this.followClick} isFollow={this.state.isFollow}/>
<QuestionCard{...this.props} iu={iu} k={k}/>
</div>:<EditProfile iu={iu} uClick={this.uClick}/>}
</div>}
</div>
	)}
	}
export default userProfile;
