import React , {Component} from 'react'
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import {Link} from 'react-router-dom';
import Slide from '@material-ui/core/Slide';
import Que1 from './que1'
import Typography from '@material-ui/core/Typography';
const styles = theme => ({
    button: {
      margin: theme.spacing.unit*5,
      backgroundColor:'rgb(21, 10, 43);',
      width:theme.spacing.unit*20
    },
    input: {
      display: 'none',
    },
  });
  
 class Page3 extends Component{
    render(){
        return(
            <div className='mt6 mt5-l ma3-l '>
            <Slide direction="right" in={true} mountOnEnter unmountOnExit>
            <Card>
            <div className='ma4'>
            <Typography variant="h5" component="h3">
            Continue the survay
          </Typography>
            <Que1/>
            </div>
         <br/> <div className='center mr-auto ml-auto'> <Link to='/survey/page3'><Button variant="contained" color="primary" className={this.props.classes.button}>
       <span className='white'> Continue</span>
      </Button></Link></div>
      </Card>
      </Slide>
            </div>
        )
    }
}
export default withStyles(styles)(Page3);