import React from 'react';

const HeadCard =({iu,k,email,isFollow,followClick,Click,edit,uClick})=>{
	return(
		<article class="mw-100-l w-100  center shadow-5  bg-white pa3 pa4-ns mv3 ba b--black-10">
		  <div class="ml3 ml0-l h5-l h6" key={k+39}>
		    {iu.pic!==null?<img alt='abc' className="br-100 h4 h5-l w4 w5-l dib fl-l ba b--black-05 pa2"  src={`${iu.pic}`}/>:<img alt='abc' className="br-100 h4 w4 dib ba b--black-05 pa2" src={`https://robohash.org/${iu.id}?200*200`}/>}
		    <dib className='flex flex-column fr-l'>
		    <h1 class="f3 fw2 fr-l mb2 mt0">{iu.name}</h1>
		    <h2 class="f5 fw4 fr-l gray mt0">{iu.branch} {iu.year}</h2>
		    <h2 class="f5 fw4 fr-l  mt0" key={k+8}><span >Followed</span > <span className='gray'> by {iu.followers}</span></h2>
		<h2 class="f5 fw4 fr-l  mt0" key={k+8}><span >Following</span ><span className='gray'> {iu.following}</span></h2>
			{iu.bio?<h2 class="f5 fw4 fr-l  mt0" key={k+8}><span >Bio</span > <span className='gray'> {iu.bio}</span></h2>:null}
		
		   	<dib className='flex flex-row mt0'>
		    <div  className=' pointer'>{isFollow===false?<p  className='w3 tc mr1 f6 grow ba  navy bb-navy pt2 pb2 mb2 dib ' onClick={()=>{followClick(true)}}>Follow</p>:<p  className='w3 pr1 pl1 f6 grow bg-navy white pt2 pb2 tc mb2 dib ' onClick={()=>{followClick(false)}}>Unfollow</p>}
			</div>
			{email===iu.email?<p  className='w3 tc mr2 f6 grow ba fr bg-navy white pt2 pb1 mb2 dib' onClick={uClick}>Edit<img src={edit} className='fr pa1  pl0' height='12' width='12' onClick={Click}/></p>:null}
			</dib>
		  	</dib>
		  </div>
		</article>
)
}


export default HeadCard;