import React from 'react';

const ChangePass =(props)=>{
	return(
		<article class="mw-100-l w-100  center shadow-5  bg-white pa3 pa4-ns mv3 ba b--black-10">
		  <div class="ml3 ml0-l h5-l h6" >
		     <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Password</label>
	            <div className="col-lg-8">
	              <input className="pa2 input-reset ba bg-transparent" onChange={props.password} type='password'/>
	            </div>
	         </div>
	             <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">New Password</label>
	            <div className="col-lg-8">
	              <input className="pa2 input-reset ba bg-transparent" onChange={props.newPass}  type='password'/>
	            </div>
	          </div>   
	             <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Confirm Password</label>
	            <div className="col-lg-8">
	              <input className="pa2 input-reset ba bg-transparent" onChange={props.confirmPass} type='password'/>
	            </div>
	         </div> 
	           <div className="flex mt3">
	            <label className="db fw4 lh-copy f6"></label>
	            <div className="col-md-8">
	              {!props.load?<input type="button" className="f6 link dim ba ph3 pv2 mb2 dib black" onClick={props.onSubmit} value="Save Changes"/>:<input type="button" className="f6 link dim ba ph3 pv2 mb2 dib black"  value="Saving"/>}
	              <span></span>
	              <input type="reset" className="f6 link dim ba ph3 pv2 mb2 dib black ml2" onClick={props.uClick} value="Cancel"/>
	            </div>
	          </div>
		  </div>
		</article>
)
}


export default ChangePass;