import React from 'react';
import Dot from './delete.jpg';
import m from '../male.png';
import f from '../female.png';
import TimeAgo from 'react-timeago';
import Switch from "react-switch";
import dislike from './notlike.png';
import like from './liked.png';
import Aoc from '../../Aoc/aoc';
import ReadMoreAndLess from 'react-read-more-less';
import Share from '../share.png'
import './boxlist.css'
import Reply from './Reply'
import {NotificationContainer, NotificationManager} from 'react-notifications';
import {
  FacebookShareButton,
    FacebookIcon,
    WhatsappShareButton,
    WhatsappIcon,
     LinkedinShareButton,
     LinkedinIcon,
	  TwitterShareButton,
	  TwitterIcon,
} from 'react-share';
var shar=false;
var userLikes=[]
const initialState={
			shar:false,
			deleteQ:false,
			deleteA:false,
			showque:false,
			reply:false,
			replyi:'',
			replyA:false,
		
		}
var likes=''
var Replies=[]
var answered:''
class Boxlist extends React.Component {	
constructor(props){
		super(props);
		this.state=initialState;
		likes=this.props.ql;
		userLikes=this.props.userLikes;
		}

	Enshowq=(a)=>{
		this.setState({showque:a})
	}
	onQuestionLike=(qid,email,i)=>{	
		fetch('https://quiet-escarpment-25332.herokuapp.com/qlike',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			qid:qid,
			email:email
		})
		}).then(response=>response.json())
		.then(data=>{
			if(data==='liked'){
				fetch('https://quiet-escarpment-25332.herokuapp.com/inc',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			qid:qid,
			inc:true
		})
		}).then(response=>response.json())
			.then(data=>{
				NotificationManager.success('Successfully', 'Liked');

          
          	this.setState({likes:likes})
			userLikes.push(qid)
			this.props.editLike(qid,true)
			console.log(this.props.ql)
			likes=this.props.ql
			this.setState({key:Math.random()})
			})
			}
			else{
				fetch('https://quiet-escarpment-25332.herokuapp.com/inc',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			qid:qid,
			inc:false
		})
		}).then(response=>response.json())
			.then(data=>{
				NotificationManager.success('Successfully', 'Disliked');
				
				userLikes=this.arrayRemove(userLikes, qid)
				this.props.editLike(qid,false)
				likes=this.props.ql
				this.setState({key:Math.random()})
			})
			}
		})
	}

	AnswerReply=(id,aid)=>{
		this.props.time(true);
		fetch('https://quiet-escarpment-25332.herokuapp.com/pa',{
				method:'put',
				headers:{'Content-Type':'application/json'},
				body:JSON.stringify({
					qid:id,
					email:this.props.user.email,
					name:this.props.uname,
					answer:answered,
					aid:aid,
				})
			}).then(data=>{
				this.props.time();
				NotificationManager.success('Successfully', 'Answer submitted');
			
			fetch('https://quiet-escarpment-25332.herokuapp.com/answer',{
			method:'get',
			headers:{'Content-Type':'application/json'},
		}).then(response=>response.json())
			.then(ans=>
				{
					this.setState({ans:ans});
				})
		}).catch(d=>{
			
  	NotificationManager.error('Error ', 'Failed submitting Answer', 5000, () => {
            alert('callback');
          });
})
	}
	arrayRemove=(arr, value)=> {
   return arr.filter(function(ele){
       return ele != value;
   });

}
onReplyChange=(event)=>{
	answered=event.target.value
}
onShowReply=(i)=>{
	this.setState({reply:!this.state.reply})
	this.setState({replyi:i})
}
onShowAnswerReply=()=>{
	this.setState({replyA:!this.state.replyA})

}

GetReplies=(id,answer)=>{
	Replies=answer.filter(reply => {
		if (reply.aid) {
  			return reply.aid===id;
  	}	})
}

	render(){
		const {match,SelectQuestion, onAnswerLike,qued,qfile,showlike, ql, scatagory,selectUser,index,qshow, ShowCall, question, qemail, qj, answer, id, qname, qyear, qbranch, qgender, onAnswerChange, onSubmitAnswer, route, r, deleteAnswer, handleChange, checked, user, show, toShow, deleteQuestion, showq, toShowq, catagory}=this.props;
		const k=45
		var count=0
		var alike=0
		var que=question;
		return(
	<div className='   w-100  w-40-l bg-white mt2 mt4-m mt0 mr-3 center-l h-50 center-m  shadow-5 overflow-hidden' >
		<p className=' ml2 mt1 mb0 grey f6 fl  pointer w-100' onClick={()=>{selectUser(qemail,qname)}}>
	{qued.pic!==null?<img src={`${qued.pic}`} height='40em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}
		{qname.toUpperCase()}, {qbranch}, {qyear}, {catagory}, {scatagory}<TimeAgo date={qj}  className='f7 black-40 fr pr3 mt1'/> </p>
		<h1 className=' w-100 f4 f4-ns f3-m f4-l black-80 ma2 pr3 pb3 fl bb b--light-silver pointer' ><span onClick={()=>SelectQuestion(id)}>Q. {que} ? </span>
			<br/>
		<div key={k+9}>{qfile!==null?<img src={`https://easystudies.herokuapp.com/${qfile}`} className='center h4 h5-l h5-m pa2' height='260'/>:null}</div>
	     <p className='ma0 pa0 pt2 f7 black-40 pointer' key={k+1}>{ql} likes</p>
	   
	     <div className='pt1'>
	    
	     {(match.path==='/home'||match.path==='/HOME')?<div >{userLikes.includes(this.props.id)?<img  src={like}
	           height='40em '
	           className='fl grow pointer'
	          onClick={()=>{this.onQuestionLike(id,user.email,index)}}/>:<img  src={dislike}
	           height='40em '
	           className='fl grow pointer'
	          onClick={()=>{this.onQuestionLike(id,user.email,index)}}
	          /> }
	          {user.verified===true?<span className='fr pa1 grow f7 f6-m f6-l grey pr2 pointer'  onClick={()=>this.onShowAnswerReply()}>{this.state.replyA?<span>   </span> :<span> Reply</span>}</span>:null}</div>:null}
	         <img className={'fr mr3'} onClick={()=>{this.setState({shar:!this.state.shar})}} src={Share}/>
	         <div key={shar}>	

	         {this.state.shar===true?
	                     <div className='fr flex pa2 bg-white shadow-5'> 
	                      <FacebookShareButton url={`https://easystudies.co/#/main/QuestionBox/${id}`}>
	                       <FacebookIcon size={32} round={true}/>
	                       </FacebookShareButton>
	                       <WhatsappShareButton url={`https://easystudies.co/#/main/QuestionBox/${id}`}>
	                       <WhatsappIcon size={32} round={true}/>
	                       </WhatsappShareButton>
	                       <LinkedinShareButton url={`https://easystudies.co/#/main/QuestionBox/${id}`}>
	                       <LinkedinIcon size={32} round={true}/>
	                       </LinkedinShareButton>
	                       <TwitterShareButton url={`https://easystudies.co/#/main/QuestionBox/${id}`}>
	                         <TwitterIcon size={32} round={true}/>
	                       </TwitterShareButton>
	                       </div>:null
	             }</div>    </div>
				{(match.path==='/home'||match.path==='/HOME')&&qemail===user.email?
										<div key={this.state.deleteQ}><img key={Math.random()} alt='' className='relative grow fr pr2 pl4 pointer' onClick={()=>{this.setState({deleteQ:!this.state.deleteQ})}} src={Dot}  height='20' width='20'/>					
							{this.state.deleteQ?<div  key={Math.random()} className='ta ba absolute bg-light-gray f7 fr ml7-l mt7-m ml6 mt v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
													<h className='pb1'>Do you want to delete the question?</h>				<br/>	<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteQuestion(id)}>	Delete</button><br/>
														<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>toShowq('none')}>	Cancel</button></div>:null}
							</div>	:<p/>}</h1>
	{this.state.showque===true?<div  className=' flex flex-column fl w-100 mt0 mb0 pt0 pb0' >
			<button className="f6 link grow ba w4 ph3 ml1 ml2-m ml3-l  pv2 mb2 dib 	mid-gray" onClick={()=>this.Enshowq(false)}>Hide replies</button>
		{answer!==undefined?<div>
				{answer.map((use,i)=>{
									if(answer[i].name==='Anonymous'){
					answer[i].branch='';
					answer[i].year='';
					answer[i].pic=null;
				}
				
				alike=answer[i].like

					this.GetReplies(answer[i].id,answer)				
					if(use.aid===null){return(<div key={Math.random()} className='answer ma2 flex flex-column w-100  mt1 mb0 bl b--black-40 ' >
															
															<p className='ma0 pa0'><span key={Math.random()} className='pointer clr ml0 grey mb0 pb0 i fl f7 f6-m f6-l' onClick={()=>{selectUser(answer[i].email,answer[i].name)}}>{answer[i].pic!==null?<img src={`${answer[i].pic}`} height='35em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}{answer[i].name.toUpperCase()}, {answer[i].branch}, {answer[i].year} 
															</span>
															<div className='flex flex-row fr'>	
												 {(match.path==='/home'||match.path==='/HOME')?<div><span className='fr pa1 grow f7 f6-m f6-l grey pr2 pointer'  onClick={()=>this.onShowReply(i)}>{this.state.reply&&this.state.replyi===i?<span>   </span> :<span> Reply</span>}</span>
												 <span className='fr pa1 grow pr2 f7 f6-m f6-l grey pointer' onClick={()=>{onAnswerLike(answer[i].id,user.email,i)}}><span className='upvote'></span> {answer[i].like} Upvotes</span>
												 		
												 		</div>	:null}
												 		<TimeAgo date={answer[i].joined}  className='f7 black-40 mb0 mt0 pb0 pt1 pr3 fr'/>	</div>	</p>
												 		
															 			 
															<span key={Math.random()} className='fl black ml2 f6 f5-m f5-l pr3 ma0 pa0 '>
															<p className='w-100 pt0 mt1 black-90'>
															{answer[i].answer} </p></span>
												         	{this.state.reply&&this.state.replyi===i?<div className='fr absolute mt4 ml7 w7 bg-white'>
												         					<textarea
												         					placeholder='Answer'
												         					onChange={this.onReplyChange}
												         					 className=' dib border-box hover-black  measure ba b--black-20 pa2  br2 mb2 h2 w-70 w-40-ns w-50-m w-60-l pa2  ba bg-transparent hover-bg-black hover-white  ml3' 
												         					
												         					/>
												         					<button className=' dib bg-near-black br1 v-top w-20 h2 pr2 white f6 grow shadow-5 bn' onClick={()=>{this.AnswerReply(id,use.id)}}>Reply</button>
												         						</div>:null}
															
															{(match.path==='/home'||match.path==='/HOME')&&answer[i].email===user.email?
																					<div className='mt0 mb0 pt0 pb0'><img key={Math.random()} alt='' className='relative grow fl pl4 pointer mt0 mb0 pt0 pb0' onClick={()=>this.setState({deleteA:!this.state.deleteA})} src={Dot}  height='20' width='20'/>
																			{this.state.deleteA?<div key={Math.random()} className='ta ba absolute f7 fl bg-light-gray  v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
																											<h className='pb1'>Do you want to delete the answer?</h>				<br/>	
																													<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteAnswer(answer[i].qid,answer[i].answer)}>	Delete</button><br/>
																										<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>this.setState({deleteA:!this.state.deleteA})}>	Cancel</button></div>:null}
																		</div>	:null}
																		<p className='mt0 mb0 ml5  pb0 pt0'></p>
															{Replies.length!==0?<div>
																{Replies.map((reply)=>{
																	return(<Reply 
																									match={match}
																									answer={reply}
																									selectUser={selectUser}
																									onAnswerLike={onAnswerLike}
																									id={id}
																									user={user}
																									i={i}
																									onShowReply={this.onShowReply}
																									showReply={this.state.reply}
																									replyid={this.state.replyi}/>)
																})		
																	
																}						</div>:null}
															</div>
															)}				})}
							
							</div>
														:<p className='tc l mt0 mb0 pt0 pb0'>No replies yet!!</p>
														}
														
	
	
		</div>:<div>{answer!==undefined?<button className="f6 link grow ba ph3 ml1 ml2-m ml3-l pv2 mb2 dib moon-gray bg-dark-gray" onClick={()=>this.Enshowq(true)}>{answer.length} Replies to Show</button>
			:<button className="f6 link grow ba ph3 ml1 ml2-m ml3-l pv2 mb2 dib moon-gray bg-dark-gray" onClick={()=>this.Enshowq(true)}>No Reply to Show</button>}<br/></div>}
	
			<br className='mt0 mb0 pt0 pb0'/>
			
			{this.state.replyA?
			<div className='dib w-100'>
			
				<textarea
				placeholder='Answer'
				 className='fl dib border-box hover-black  measure ba b--black-20 pa2  br2 mb2 h2 w-70 w-40-ns w-50-m w-60-l pa2  ba bg-transparent hover-bg-black hover-white  ml3' 
				onChange={onAnswerChange}/>
				<button className='fl dib bg-near-black br1 v-top w-20 h2 pr2 white f6 grow shadow-5 bn' onClick={()=> onSubmitAnswer(id)}>Reply</button>
				
			</div>	
		:null
	}</div>
	)}
}

export default Boxlist;