import React from 'react';

const ConfirmOtp=({load, Loading, onSubmitOtp, onOtpChange})=>{
return(
<article className="br3 tc bg-white ba black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">
<main className="pa4 black-80">
		<div className="measure center">
		<Loading color="bg-near-black" load={load} x='25%' y='25%'/>
	<legend className="f1 fw6 ph0 mh0">Verify Otp</legend>
		<label className="db fw6 lh-copy f6" htmlFor="otp">Otp</label>
		<input 
		className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="number" 
		name="otp"  
		id="otp"
		onChange={onOtpChange}
		/>

		<input className="b ph3 pv2 input-reset ba b--black bg-transparent mt3 grow pointer f6 dib" onClick={onSubmitOtp} type="submit" value="Submit Otp"/>
		
		</div>
</main>
</article>
)
}

export default ConfirmOtp;