import React from 'react'
import FormData from 'form-data';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import ChangePass from './Card/ChangePass';
import Model from '../../../Model/Model1'
class  EditProfile extends React.Component {	
constructor(props){
		super(props);
		this.state={
			file:'',
   		   formdata: new FormData(),
   		   load:false,
   		   name:'',
   		   number:'',
   		   pass:'',
   		   newPass:'',
   		   conPass:'',
   		   gender:'',
   		   branch:'',
   		   bio:'',
   		   email:'',
   		   year:"",
   		   college:"",
   		   pic:'',
   		   verified:'',
   		   checkedm:false,
			checkedf:false,
			show:false,

	}

}
getData=()=>{
	this.setState({name:this.props.iu.name});
	this.setState({number:this.props.iu.number});
	this.setState({branch:this.props.iu.branch});
	this.setState({bio:this.props.iu.bio});
	this.setState({year:this.props.iu.year});
	this.setState({college:this.props.iu.college});
	this.setState({email:this.props.iu.email});
	this.setState({pic:this.props.iu.pic});
	this.setState({verified:this.props.iu.verified});
	if(this.props.iu.gender==='m'){
		this.setState({checkedm:true})
	}else{
		this.setState({checkedf:true})
	}
}

componentWillMount(props) {
		this.getData()
		
}

picChose=(event)=>{
	this.setState({ pic: URL.createObjectURL(event.target.files[0])})
	this.setState({file:event.target.files[0]})

}

NameChange=(event)=>{
	this.setState({name:event.target.value})
}
NumberChange=(event)=>{
	this.setState({number:event.target.value})
}
BranchChange=(event)=>{
	this.setState({branch:event.target.value})
}
BioChange=(event)=>{
	this.setState({bio:event.target.value})
}
YearChange=(event)=>{
	this.setState({year:event.target.value})
}
CollegeChange=(event)=>{
	this.setState({college:event.target.value})
}
EmailChange=(event)=>{
	this.setState({email:event.target.value})
}
passChange=(event)=>{
	this.setState({pass:event.target.value})
}
genderChange=(s)=>{
	if(s==='f'){
	 		this.setState({checkedm:false})
	 		this.setState({checkedf:true})
	 		this.setState({gender:'f'})
	 	}
	 	if(s==='m'){
	 		this.setState({checkedm:true})
	 		this.setState({checkedf:false})
	 		this.setState({gender:'m'})
	 	}
	 	this.setState({gender:s})
}

onShow=()=>{
	this.setState({show:!this.state.show})
}
pass=(event)=>{
	this.setState({pass:event.target.value})
}
newPass=(event)=>{
	this.setState({newPass:event.target.value})
}
confirmPass=(event)=>{
	this.setState({conPass:event.target.value})
}
onPassSubmit=()=>{
	if (this.state.newPass!==this.state.conPass) {

	}else{
		fetch('https://quiet-escarpment-25332.herokuapp.com/newpass',{
								method:'post',
								headers:{'Content-Type':'application/json'},
								body:JSON.stringify({
									email:this.props.iu.email,
									password:this.state.newPass,
									prevPassword:this.state.pass
								})
							}).then(response=>response.json())
							.then(d=>{NotificationManager.success('Sucessful', 'Saved Changes')
								this.onShow()})
							.catch(err=>{NotificationManager.error('Error ', 'Failed', 5000, () => {
		            alert('callback');
				})
				
		          })
	}}

onSubmit=()=>{
		this.setState({load:true})
		var formdataU= new FormData();
		
		fetch('https://quiet-escarpment-25332.herokuapp.com/changedetail',{
							method:'post',
							headers:{'Content-Type':'application/json'},
							body:JSON.stringify({
								newEmail:this.state.email.toLowerCase(),
								email:this.props.iu.email,
								name:this.state.name,
								number:this.state.number,
								branch:this.state.branch,
								year:this.state.year,
								gender:this.state.gender,
								bio:this.state.bio,
								college:this.state.college,
								prevPassword:this.state.pass
							})
						}).then(response=>response.json())
						.then(data=>{
							if(data==='sucess'){if(this.state.file!==''){
		formdataU.append('image',this.state.file);
		formdataU.append('uid',this.props.iu.id);
			fetch('https://quiet-escarpment-25332.herokuapp.com/upload',{
				method:'post',
				body:formdataU
				}).then(d=>{
						this.setState({load:false})
								NotificationManager.success('Sucessful', 'Saved Changes')
						
							}).catch(err=>{NotificationManager.error('Error ', 'Failed Uploading profile pic', 5000, () => {
		            alert('callback');
				})
				
		          });
							}else{this.setState({load:false})
								NotificationManager.success('Sucessful', 'Saved Changes')}	}	else{NotificationManager.error('Error ', 'Failed ', 5000, () => {
		            alert('callback');
		          });}			
				
		}).catch(d=>{NotificationManager.error('Error ', 'Failed ', 5000, () => {
		            alert('callback');
		          });
			})
		

	}


	render(){
		const {pic,name,pass,gender,year,branch,email,bio,college,number,checkedf,checkedm}=this.state;
		return(
			<div className="pa4 black-80   center w-100">
	    <h1>Edit Profile</h1>
	  	<hr/>
		<div className="flex flex-row-l  flex-column ">
	      
	      <div className="flex flex-column">
	        <div className="">
	          <img src={pic} className="avatar br-100 w4 h4" alt="avatar"/>
	          <h6>Upload a different photo...</h6>
	          
	          <input onChange={this.picChose} type="file" className="form-control" />
	        </div>
	      </div>
	      
	     
	      <div className="flex flex-column personal-info">
	        <div className='flex'>
	        <h3>Personal info</h3> <legend className="bg-navy white pa2 mt2 ml1 pointer" onClick={this.onShow}>Change Password</legend>
	        <Model className={'Modal1 mt0'} show={this.state.show}><ChangePass 
	        password={this.pass}
	        newPass={this.newPass}
	        confirmPass={this.confirmPass}
	        onSubmit={this.onPassSubmit}
	        uClick={this.onShow}/></Model>
	        </div>
	        <form className="form-horizontal" role="form">
	          <div className="flex flex-row-l">
	            <label className="db fw4 lh-copy f5 mr2">Name:</label>
	            <div className="col-lg-8">
	              <input className=" pa2 input-reset ba bg-transparent" onChange={this.NameChange} type="text" value={name}/>
	            </div>
	          </div>
	          <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Number:</label>
	            <div className="col-lg-8">
	              <input className="pa2 input-reset ba bg-transparent" onChange={this.NumberChange} value={number} type='number'/>
	            </div>
	          </div>
	          <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Bio:</label>
	            <div className="col-lg-8">
	              <textarea 
					onChange={this.BioChange}
					className='  h3 w7 pa0 input-reset ba bg-transparent'
					value={bio}
				 />
	            </div>
	          </div>
	          <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Email:</label>
	            <div className="col-lg-8">
	              <input className="pa2 input-reset ba bg-transparent" type="text" value={email}
	              onChange={this.EmailChange}/>
	            </div>
	          </div>
	          <label className="dib  fw6 lh-copy f6 ml2 mr2 mt2 di  black-60 " htmlFor="password">Gender</label>
  <label className="dib  fw6 lh-copy f6 ml2 mr2  black-70 " >Male</label><input type='radio' value='male' checked={checkedm} onChange={()=>this.genderChange('m')}/>
	 <label className="dib  fw6 lh-copy f6 ml2 mr2  black-70 " >Female</label><input type='radio' value='female' checked={checkedf} onChange={()=>this.genderChange('f')}/>
		
	              <div className="flex mt3">
	          <label className=" db fw4 lh-copy f6 mr2" htmlFor="year">Year</label>
			<select name='Year'
			value={year}
			onChange={this.YearChange}
			>
	    <option value="I yr.">I</option>
	    <option value="II yr.">II</option>
	    <option value="III yr.">III</option>
	    <option value="IV yr.">IV</option>
	    	
			</select>
			</div>
			 <div className="flex mt3">
			<label className=" db fw4 lh-copy f6 mr2" htmlFor="year">College</label>
		<select name='college'
		value={college}
		onChange={this.CollegeChange}
		>
    <option value="GCOEA">GCOEA</option>
    
    	
		</select>
		</div>
	          <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Branch:</label>
	            <div className="col-lg-8">
	              <div className="ui-select">
	                <select id="user_time_zone" className="form-control"
	                value={branch}
	                onChange={this.BranchChange}>
	                  <option value="I.T.">I.T.</option>
					    <option value="C.S.">C.S.</option>
					    <option value="Instru">Instru</option>
					    <option value="Mech">Mech</option>
					    <option value="Civil">Civil</option>
					    <option value="EXTC">EXTC</option>
					    <option value="ELPO">ELPO</option>
	                </select>
	              </div>
	            </div>
	          </div>
	          <div className="flex mt3">
	            <label className="db fw4 lh-copy f6 mr2">Password:</label>
	            <div className="col-md-8">
	              <input className=" pa2 input-reset ba bg-transparent" type="password" onChange={this.passChange} value={pass}/>
	            </div>
	          </div>
	          <div className="flex mt3">
	            <label className="db fw4 lh-copy f6"></label>
	            <div className="col-md-8">
	              {!this.state.load?<input type="button" className="f6 link dim ba ph3 pv2 mb2 dib black" onClick={this.onSubmit} value="Save Changes"/>:<input type="button" className="f6 link dim ba ph3 pv2 mb2 dib black"  value="Saving"/>}
	              <span></span>
	              <input type="reset" className="f6 link dim ba ph3 pv2 mb2 dib black ml2" onClick={this.props.uClick} value="Cancel"/>
	            </div>
	          </div>
	        </form>
	      </div>
	  </div>
	</div>
	
			)}
}
export default EditProfile;