import React from 'react'
import UserProfile from './userprofile/userprofile'
import Model from '../../Model/Model';

const Profile=(props)=>{
	return(
		<Model className={"Modal2"} show={true} modalClosed={props.modalClosed}>
		<UserProfile {...props} onsign={props.onsign} user={props.user} allUsers={props.allUsers} time1={props.time1} k={props.k} email={props.email} s={props.s} Click={props.Click} picChose={props.picChose} uploadClick={props.uploadClick} users={props.users} modalClosed={props.modalClosed} iu={props.iu} followClick={props.followClick} userFollowing={props.userFollowing} checkFollow={props.checkFollow} isFollowing={props.isFollowing}/>
		</Model>
		)}
		export default Profile;