import React from 'react';
import TimeAgo from 'react-timeago';

const Reply=({answer,match,selectUser,onAnswerLike,id,user,onShowReply,i,showReply,replyid})=>{
return(
	<div>
			<div key={Math.random()} className='answer ma2 ml3 flex flex-column w-100  mt1 mb0 bl b--black-40 ' >
						
						<p className='ma0 pa0'><span key={Math.random()} className='pointer clr ml0 grey mb0 pb0 i fl f7 f6-m f6-l' onClick={()=>{selectUser(answer.email,answer.name)}}>{answer.pic!==null?<img src={`${answer.pic}`} height='35em' className='pa1 br-100 pt0 pb0'/>:<img  src={`https://robohash.org/${id}?200*200`} height='26.7em' className='pa1 pt0 pb0'/>}{answer.name.toUpperCase()}, {answer.branch}, {answer.year} 
						</span>
						<div className='flex flex-row fr'>	
			 {(match.path==='/home'||match.path==='/HOME')?<div><span className='fr pa1 grow f7 f6-m f6-l grey pr2 pointer'  onClick={()=>onShowReply(i)}>{showReply&&replyid===i?<span className='f5 black'>   </span> :<span> Reply</span>}</span>
			 <span className='fr pa1 grow pr2 f7 f6-m f6-l grey pointer' onClick={()=>{onAnswerLike(answer.id,user.email)}}><span className='upvote'></span> {answer.like} Upvotes</span>
			 		
			 		</div>	:null}
			 		<TimeAgo date={answer.joined}  className='f7 black-40 mb0 mt0 pb0 pt1 pr3 fr'/>	</div>	</p>
			 		
						 			 
						<span key={Math.random()} className='fl black ml2 f6 f5-m f5-l pr3 ma0 pa0 '>
						<p className='w-100 pt0 mt1 black-90'>
						{answer.answer} </p></span>{/*
						{(match.path==='/home'||match.path==='/HOME')&&answer.email===user.email?
												<div className='mt0 mb0 pt0 pb0'><img key={Math.random()} alt='' className='relative grow fl pl4 pointer mt0 mb0 pt0 pb0' onClick={()=>this.setState({deleteA:!this.state.deleteA})} src={Dot}  height='20' width='20'/>
										{this.state.deleteA?<div key={Math.random()} className='ta ba absolute f7 fl bg-light-gray  v-mid  u bw2 tc h4 mb2 pt2 navy w4 ml4' >
																		<h className='pb1'>Do you want to delete the answer?</h>				<br/>	
																				<button className='bg-transparent mt3 b red grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>deleteAnswer(answer[i].qid,answer[i].answer)}>	Delete</button><br/>
																	<button className='bg-transparent mt3 b  grow mt1 ba w-80 center b--light-purple  pointer' onClick={()=>this.setState({deleteA:!this.state.deleteA})}>	Cancel</button></div>:null}
									</div>	:null}*/}
			</div>						
</div>)
}

export default Reply;