import React from 'react';
import search from './search.jpg';
import  './mbmenu.css';
import Switch from "react-switch";
import Backdrop from '../../Backdrop/Backdrop'

const MbMenu = ({menuCall,handleChange, checked,menu,catagorySelect, yearSelect, questionChange, questionSearch, pSelect}) => {
return(
	<div>
	   <Backdrop show={menu} className={'Backdrop1'}  clicked={menuCall}/>
	<div className='mbmenu flex flex-column br bclr black-80 white mt0 fixed w-70 w-20-l z-9999' 
	style={{
                transform: menu? 'translatex(0)' : 'translatex(-100vh)',
                opacity: menu? '1' : '0'
            }}>
 <div className='dib mt3'>
 <input 
		className=" b pa2 input-reset ba bg-white hover-bg-black hover-white w-80" 
		type="text"
		name="question"  
		id="question"
		placeholder='Search Question'
		onChange={questionChange}
		/>
	<p className='w-20 dib '>	<img src={search} 
	onClick={questionSearch}
	className='ml3 grow' height='27' width='27'/></p>
</div>
<div className='bclr white'>
 <h1 className='f4 b  db pl3'> <span className='pr3'>Anonymous:</span>
			       
			        <Switch
			        className=''
			         height={17}
			    	width={48}
			          onChange={handleChange}
			          checked={checked}
			          id="material-switch"
			        />  </h1>
 <h1 className='f3 b  db pl3'> Categories</h1>
 <a className=' db grow pointer pa2  pl3 ' onClick={()=>catagorySelect("Stupid Section")}> Stupid Section</a>
 <a className=' db grow pointer pa2  pl3' onClick={()=>catagorySelect("Technical")}>Technical</a>
 <a className=' db grow pointer pa2  pl3' onClick={()=>catagorySelect("Non-Technical")}>Non-Technical</a>
 <a className=' db grow pointer pa2  pl3' onClick={()=>catagorySelect("Confession")}>Confession</a>
 <a className=' db grow pointer pa2  pl3' onClick={()=>catagorySelect("interview")}>Interview</a>
  <a className=' db grow pointer pa2  pl3' onClick={()=>catagorySelect("Not Specified")}>Not Specified</a>
  <h1 className='f3 b  db pl3'> Years </h1>
 <a className=' db grow pointer pa2  pl3' value='1' onClick={()=>yearSelect("I yr.")}>First Year</a>
 <a className=' db grow pointer pa2  pl3' value='2' onClick={()=>yearSelect("II yr.")}>Second Year</a>
 <a className=' db grow pointer pa2 pl3' value='3' onClick={()=>yearSelect("III yr.")}>Third Year</a>
 <a className=' db grow pointer pa2  pl3' value='4' onClick={()=>yearSelect("IV yr.")}>Fourth Year</a>
   <h1 className='f3 b  db pl3'> Other </h1>
 <a className=' db grow pointer pa2  pl3' value='1' onClick={()=>pSelect('{user.branch},{user.year},c')}>My Class</a>
 <a className=' db grow pointer pa2  pl3' value='2' onClick={()=>pSelect("{user.branch},,b")}>My Branch</a>
 <a className=' db grow pointer pa2 pl3 mb5' value='3' onClick={()=>pSelect("{user.year},,y")}>My Year</a>

	</div>
	</div>
	</div>
	)
	}
export default MbMenu;