import React from 'react';
import Discription from '../discription';
import Footer from '../footer.js';
import Loading from '../loading/loading';
import Form from './form';
import EmailOtp from './emailotp';
import ConfirmOtp from './confirmOtp';
import ChangePass from './changepass'
import './signin.css'
const initialuser={
			signinEmail:'',
			signNumber:'',
			signinPassword:'',
			err:'',
			route:'form',
			otp:'',
			confirmPassword:'',
			value:''
}

var email;

class Signin extends React.Component{
	constructor(props) {
		super(props);
		this.state=initialuser;
	 }

	changingRoute=(d)=>{
		this.setState({route:d})
	}
onKeyPress = (e) => {
        if(e.key === 'Enter'){
           this.Onsubmitsignin()     }
    }
   

	 error=(err)=>{
	 	this.setState({err:err});
	 }

	OnEmailChange = (event) =>{
		 if (event.which === 13) {
      // Do code here
    	console.log('pond')
    }
	this.setState({signinEmail:event.target.value})
	this.setState({signNumber:event.target.value})
	}

    OnPasswordChange = (event) =>{

		this.setState({signinPassword:event.target.value})
	}

	onOtpChange=(event)=>{
		this.setState({otp:event.target.value})
	}

	onConfirmPasswordChange=(event)=>{
		this.setState({confirmPassword:event.target.value})
	}

	onSubmitPassword=()=>{
		this.props.time(true);
		if(this.state.signinPassword===this.state.confirmPassword){
		this.error('')
		fetch('https://quiet-escarpment-25332.herokuapp.com/changepass', {
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			email:this.state.signinEmail.toLowerCase(),
			password:this.state.confirmPassword
		})
	}).then(response=>response.json())
		.then(data=>{
			if(data==='sucess'){
				this.props.onResponding('Password changed sucessfully')
				this.changingRoute('form')
				this.props.time();
			}
		}).catch(er=>{this.error('error changing password please try again')
	this.props.time();}
		)
}else{this.props.onResponding('Password does not match')}	}

	onSubmitOtp=()=>{
		this.props.time(true);
		this.error('')
		console.log(this.state.signinEmail)
		fetch('https://quiet-escarpment-25332.herokuapp.com/confirmotp', {
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			email:this.state.signinEmail,
			vid:this.state.otp
		})
	}).then(response=>response.json())
		.then(data=>{
			if(data==='sucess'){
				this.props.onResponding('Otp verified')
				this.changingRoute('changepass')
				this.props.time();
			}else{this.error('Incorrect otp')
				this.props.time();}
		}).catch(r=>{this.error('Incorrect otp')
			this.props.time()}
		)
	}

onSubmitEmail=()=>{
		this.props.time(true);
		this.error('')
			email=this.state.signinEmail.toLowerCase();
		fetch('https://quiet-escarpment-25332.herokuapp.com/otpsend', {
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			email:this.state.signinEmail.toLowerCase()
		})
	}).then(response=>response.json())
		.then(data=>{
			if(data==='sucess'){
				email=this.state.signinEmail.toLowerCase()
				console.log(email)
				this.props.onResponding('Otp send to your email')
				this.changingRoute('confirmotp')
				this.props.time();
			}
		}).catch(er=>{this.error('Incorrect email')
	this.props.time();}
		)
}
	Onsubmitsignin=()=>{
		this.props.time(true);
		this.error('')
		fetch('https://quiet-escarpment-25332.herokuapp.com/signin', {
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			email:this.state.signinEmail.toLowerCase(),
			number:this.state.signNumber,
			password:this.state.signinPassword
		})
		}).then(response=>response.json())
		.then(data=>{
			if(data==='sucess'){
				
				fetch('https://quiet-escarpment-25332.herokuapp.com/user',{
			method:'post',
			headers:{'Content-Type':'application/json'},
			body:JSON.stringify({
			email:this.state.signinEmail.toLowerCase(),
			number:this.state.signNumber
		})
		}).then(response=>response.json())
			.then(user=>
				{
					this.props.userd(user);
					this.props.onResponding('Signing in')
					this.props.onsign('HOME');
					this.props.time();

				})
		
			}else{
				this.error('Incorrect Email or Password')
				this.props.onResponding('Incorrect Email or Password')
				this.props.time();
			}
		}).catch(er=>{this.error('Incorrect Email or Password!!')
		this.props.time()})
	

		
	 
	}
	render(){
		const {onsign, time, load, insign}=this.props;
	return(
		<div className="App">
 		<p className='red w-33 h2 f4 center'>{this.state.err}</p>
		{this.state.route==='form'?<Form  load={load}
		changingRoute={this.changingRoute}
		 onKeyPress={ this.onKeyPress}
				 OnEmailChange={this.OnEmailChange}
				 OnPasswordChange={this.OnPasswordChange}
				 insign={insign}
				 Onsubmitsignin={this.Onsubmitsignin}
				 Loading={Loading}/>
		
				:(this.state.route==='emailotp'? <EmailOtp
								 load={load}
								 OnEmailChange={this.OnEmailChange}
								 onSubmitEmail={this.onSubmitEmail}
								 Loading={Loading}/>
						
								 :(this.state.route==='confirmotp'?<ConfirmOtp load={load} 
								 Loading={Loading} 
								 onSubmitOtp={this.onSubmitOtp}
								  onOtpChange={this.onOtpChange} />
						
								  :(this.state.route==='changepass'?<ChangePass load={load}
								  								   Loading={Loading}
								  								    onSubmitPassword={this.onSubmitPassword}
								  								     OnPasswordChange={this.OnPasswordChange}
								  								      onConfirmPasswordChange={this.onConfirmPasswordChange}/>:null)))}

</div>
		);
}
}
export default Signin;
