import React , {Component} from 'react'
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import {Link} from 'react-router-dom';
import Slide from '@material-ui/core/Slide';
const styles = theme => ({
    button: {
      margin: theme.spacing.unit*5,
      backgroundColor:'rgb(21, 10, 43);',
      width:theme.spacing.unit*20
    },
    input: {
      display: 'none',
    },
  });
  
 class Page1 extends Component{
    render(){
        return(
            <div className='mt6 mt7-l ma3-l'>
            <Slide direction="right" in={true} mountOnEnter unmountOnExit>
            <Card>
         <div className='ma3'>   <div className='f3 fw5'>Hey you are on the survey page of Easystudies.co</div>
            <div className='f4 pt2'>We need your help to improve the services and provide best to you.</div>
           <br/> <div className='f4 pt2'>We will be sharing some ideas where you can choose if you liked it or not.</div>
            
            <br/><div className='f5 pt3'>So first let me introduce myself i am Easystudies a web app right now aiming for engineers where they can ask and answer the questions.</div>                
           <br/> </div> <br/> <div className='center mr-auto ml-auto'> <Link to='/survey/page2'><Button variant="contained" color="primary" className={this.props.classes.button}>
       <span className='white'> Get Started</span>
      </Button></Link></div>
      </Card>
      </Slide>
            </div>
        )
    }
}
export default withStyles(styles)(Page1);