import React from 'react';
import './loading.css';
import Logo from '../../logo.png'
const Loading = ({color, load, x, y}) => (
	<div  className='pt0 loading  z-max'>
	{load===true?
		<div className="stage  ma7" >
		<p className="spinner br3 v-mid pt2" style={{textAlign: 'center', backgroundColor: '#212121'}}><img width='30px' height='30px' src={Logo}/></p>
	</div>:<p/>}
	</div>
	)
export default Loading;