import React from 'react';
import './introduction.css';
import connect from './connect.png';
import QnA from './qna.png';
import Anonymus from './anonymus.png'
import robot from './robot-illustration.png'

const Introduction= ({mainClick,insign})=>{
	return(
		<div>
		<div className='introduction b1 '><h2 className="App-title black relative f3 mt0 pt4 pb0 mb0 tc f2-ns f2-m f1-l">Welcome to Easystudies.co</h2>

                 <p className='white pt4 tc f3-l f3-m f4'>A small initiative aiming towards making the community of GCOEA more integrated and robust</p>
                   <p className='tc mt3 mt4-m mt5-l white pa2'> <a> <button className='button ' onClick={()=>insign('Register')}>Register</button>  </a><a><button className='button ml4' onClick={()=>insign('signin')}>Sign In</button> </a></p>
                    </div>
<div className='introduction b2 '>

                 <p className='white tc mt0 pt4 f2-l f2-m f3'>Connect yourself with all other GCOEANs</p>
                    <img src={connect} className='ma0 ml7-l ml6-m ml4 pa0 h5-l h5-m h4'  height='200'/></div>
 <div className='introduction  b3'>

                 <p className='white tc mt0 pt4 f2-l f2-m f3'>Ask anonymously Without revealing identity</p>
                    <img src={Anonymus} className='ma0 fr mr7-l mr6-m mr4 pa0 h5-l h5-m h5'  height='300'/></div>
 <div className='introduction  b4 '>
                 <p className='white tc mt0 pt4 f2-l f2-m f3'>Have a QnA with GCOEANs</p>
                    <img src={QnA} className='ma0 ml7-l ml6-m ml4 h5-l h5-m h5 pa0'  height='200'/></div>
 <div className='end  bg-white '><h2 className="App-title white relative f3 mt0 pt4 pb0 mb0 tc f2-ns f2-m f1-l"></h2>
                  <a className='button1 grow'  onClick={()=>insign('main')}>Get Started</a> 
                    
                   </div>

		</div>
)
}
export default Introduction