import React from 'react';
import arrow from './arrow.png'
const Form=({onKeyPress,load, Loading,changingRoute, OnEmailChange, OnPasswordChange, insign, Onsubmitsignin})=>{
return(
<article className="br3 tc bg-white ba black-10 mv4 w-100 w-50-m w-25-l mw6 center shadow-5">

		<legend className="bg-navy signin white ">Sign In</legend>
<main className="pa4 black-80">
		<div className="measure center">
		<fieldset id="sign_up" className="ba b--transparent ph0 mh0">
		<div className="mt3">
		<input 
		className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="email" 
		name="email-address"  
		id="email-address"
		placeholder="Email"
		onChange={OnEmailChange}
		onKeyPress={ onKeyPress}
		/>
		</div>
		<div className="mv3">
		<input 
		className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" 
		type="password"
		name="password"  
		id="password"
		placeholder="Password"
		onChange={OnPasswordChange}
		onKeyPress={ onKeyPress}
		/>
		</div>
		</fieldset>
		<input type='submit' className='bg-navy b--navy pointer pa1 white br2 h2 w3 center' onClick={Onsubmitsignin}
		value="Signin"/>

		<div className="lh-copy mt3">
		<p  onClick={() => insign('Register')} className="f6 link dim black db pointer">Register</p>
		<p  onClick={() => changingRoute('emailotp')} className="f6 link dim black db pointer">Forgot Password?</p>
		</div>
		</div>
</main>
</article>
)
}

export default Form;
