import React , {Component} from 'react'
import Bar from './Bar'
import Page1 from './Page1'
import Page2 from './Page2'
import Page3 from './Page3/Page3'
import {Route, Switch} from 'react-router-dom';
export default class Servay extends Component{
    render(){
        return(
            <div className='tc'>
                <Bar/>
                <Switch>
        <Route path='/survey' exact render={(props)=>(<Page1/>)} />
        <Route path='/survey/page2' exact render={(props)=>(<Page2/>)} />
        <Route path='/survey/page3' exact render={(props)=>(<Page3/>)} />
        
        </Switch>
        </div>
        )
    }
}