import React, { Component } from 'react'
import './search.css'


var source=''

export default class SearchExampleStandard extends Component {
  componentWillMount() {
  this.resetComponent()
  }

   resetComponent = () => this.setState({  results: [], value: '' })

   handleSearchChange = (event) => {
    this.setState({ value: event.target.value })

    setTimeout(() => {
      if (this.state.value.length === 0) return this.resetComponent()

    if(this.props.users){
          source=this.props.users.filter(users => {
      			return users.name.toLowerCase().includes(this.state.value.toLowerCase());
      		})
      }
 

      this.setState({
        isLoading: false,
        results:source,
      })
    }, 300)
  }
  onclick=(result)=>{
  	this.props.getUser(result)
  	this.resetComponent()
  }

  render() {

    return (
     <div id="wrap">
<form action="" className='form'>
  <input type="search" className='input' onChange={this.handleSearchChange}/>
  <i className="fa fa-search"></i>

{this.state.results!==[]?<main className="mw6 box center w5 bg-white black absolute mt4 shadow-5 br3 ">
{this.state.results.map((result,i)=>{
	if(i<6){return(
			 <article className="dt w-100 bb b--black-05 pb2 mt2" href="#0" onClick={()=>{this.onclick(result)}}>
	     
	      <div className="dtc v-mid pl3">
	        <h1 className="f6 f5-ns fw6 lh-title black mv0">{result.name} </h1>
	        <h2 className="f6 fw4 mt0 mb0 black-60">{result.branch} {result.year}</h2>
	      </div>
	   
	    </article>
		)}
		else{return(null)}
})}	
</main>:null}
</form>
</div>
    )
  }
}
