import React from 'react';
import Switch from "react-switch";
import w from '../warning.png';
import './questioninput.css';
import attach from '../attach.png'
import Model from '../../Model/Model';
const QuestionInput=(props)=>{
	return(
	<div>
			{props.user.verified===true?
				<div className='black-80 w-100 w-60-m w-60-l mt0 mr-3 center-l bg-white center-m shadow-5'>
									
						
									<h1 className='f4 f4-ns f3-m f2-l pl2 dib w-100 tc relative'>Ask Your Question</h1>
									<textarea 
									placeholder='Question?'
									className='center input-reset ml2 dib border-box hover-black measure ba b--black-20 pa2 br2 mb2 h2 w-70 w-40-ns w-50-m w-60-l pa2 input-reset ba bg-transparent hover-bg-black hover-white  ml3'
									onChange={props.onAsking} />
									<button className='center dib bg-near-black br1 v-top w-20 h2 pr2 white grow shadow-5 bn'
									onClick={props.onSubmitAsk}> Ask </button>
									<img className='pl2 dib grow' onClick={props.clickAttach} height='30px' src={attach}/>
									<Model show={props.show} className={'Modal mt5'}  modalClosed={props.clickAttach}><input onChange={props.fileChose} type='file' key='image' id='image' name='image '></input></Model>
									<br/>
									<div className=' f7 f6-m f6-l flex'>
					<label className=" fw6 lh f7 f6-m f6-l  fl " htmlFor="password" >Anonymous: 
			       
			        <Switch
			        className=''
			         height={17}
			    	width={48}
			          onChange={props.handleChange}
			          checked={props.checked}
			          id="material-switch"
			        />
			        </label>          
								<select name='Category'
								value='Category'
								placeholder="Category"
								className='mr2 w3 w4-l w4-m br3'
									onChange={props.onCatagoryChange}>
							<option value="Stupid Section">Stupid Section</option>
						    <option value="Technical">Technical</option>
						    <option value="Non-Technical">Non-Technical</option>
						    <option value="Confession">Confession</option>  
						    <option value="Interview">Interview</option>
						    <option value="Not specified">Not specified</option>
						    
						        </select>
						        	<input 
		className=" b pa2 input-reset ba bg-white h1 hover-bg-black hover-white w-30" 
		type="text"
		name="question"  
		id="question"
		placeholder='Sub-Category'
		onChange={props.subCatagoryChange}
		/>
						    </div>
			       
			        </div>:<p className='f4-l f4-m f5 tc i red pa3 '><img height='20em' src={w} />To ask question or reply on answer you need to verify your email. Please check your mail box and click on the verification link . To get verification link again <p className=' dib f6 link dim u  bw1 ph3 pv2 mb2 pointer dark-blue' onClick={props.resendLink}>click here</p>
			        Kindly refresh after verification </p>}
						</div>
)}
export default QuestionInput;