import React from 'react';
import TimeAgo from 'react-timeago';
import Loading from '../../../../loading.svg';
const intialState={
	ques:''
}
class QuestionCard extends React.Component{
	constructor(props){
		super(props);
		this.state=intialState
		this.getQue()
	}
	getQue=()=>{
		fetch('https://quiet-escarpment-25332.herokuapp.com/profileq',{
							method:'post',
							headers:{'Content-Type':'application/json'},
							body:JSON.stringify({
								email:this.props.iu.email,
								name:this.props.iu.name					
							})
						}).then(response=>response.json())
						.then(data=>{
							this.setState({ques:data});
						})
	}
onClick=(id)=>{

	var str = this.props.location.pathname;
	str=str.substr(0,5);
		this.props.history.push(`${str}/QuestionBox/${id}`)
	console.log(str)
}
render(){
	return(
		<article className="mw-100-l w-100 flex-l flex-row-l  center bg-white shadow-5 pa3 pa4-ns mv3 ba b--black-10">
				<legend className="bg-navy v-mid pa1 pointer questionCard h2  white ">Questions</legend>
				{this.state.ques===undefined||this.state.ques===''?<div className=' w-100 w-50-m w-40-l h4 tc pa4 mt3 mt4-m mt0 mr-3 center-l h-50 center-m  overflow-hidden' >
			<img src={Loading} height='50px' width='50px'/>
			</div>:
				<div>
				{
					this.state.ques.map((que)=>{
						return(
							<div className='b--black-20 bb w-100 pointer' onClick={()=>this.onClick(que.id)}>
									<p className='black' >Q. {que.question}</p>
								<span className='f5-l f6 fw4  gray'>{que.like} likes</span> <span className='f5-l f6 fw4 fr gray'><TimeAgo date={que.joined} /> </span>
							</div>)
					})
				}
				</div>
				}
		</article>									
		)}
}
export default QuestionCard;